import React from "react";
import AppContext from "../../context";
import Main from "../../Components/Main/Main";

const MainView = () => (
	<AppContext.Consumer>
		{(context) => (
			<Main
				dogs={context.dogs}
				dogsIndex={context.dogsIndex}
				handlerX={context.handleClickX}
				handlerHeart={context.handleClickHeart}
			></Main>
		)}
	</AppContext.Consumer>
);

export default MainView;
