import React from "react";
import styled from "styled-components";
import ListItem from "../List/ListItem/ListItem";

const Container = styled.ul`
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	list-style: none;

	@media (max-width: 837px) {
		justify-content: center;
	}
`;

const Heading = styled.h2`
	color: red;
	font-size: 25px;
	text-align: center;

	@media (max-width: 700px) {
		font-size: 15px;
	}
`;

const List = ({ dogs }) => (
	<>
		{dogs.length ? (
			<Container>
				{dogs.map((dog) => (
					<ListItem key={dog.index} {...dog} />
				))}
			</Container>
		) : (
			<Heading>
				There's nothing here yet, pleas choose some dogs!
			</Heading>
		)}
	</>
);

export default List;
