import React from "react";
import styled from "styled-components";

const StyledParagraph = styled.p`
	font-size: 20px;
	color: black;

	@media (max-width: 600px) {
		font-size: 18px;
	}
`;

const SmallStyledP = styled.p`
	font-size: 15px;
`;

const Paragraph = ({ children, info, secondary }) => {
	return (
		<>
			{secondary ? (
				<SmallStyledP>
					{info}
					{children}
				</SmallStyledP>
			) : (
				<StyledParagraph>
					{info}
					{children}
				</StyledParagraph>
			)}
		</>
	);
};

export default Paragraph;
