import React, { Component } from "react";
import "./App.css";
import AppContext from "../../context";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import Header from "../../Components/Header/Header";
import { ThemeProvider } from "styled-components";
import { theme } from "../../theme/mainTheme";
import SpanielPhoto from "../../assets/spaniel.jpg";
import ShibaPhoto from "../../assets/shiba.jpg";
import OwczarekPhoto from "../../assets/owczarek.jpeg";
import HuskyPhoto from "../../assets/husky.jpg";
import ShiztzuPhoto from "../../assets/shiz-tzu.jpg";
import SznaucerPhoto from "../../assets/miniaturowy-sznaucer.jpg";
import KundelekPhoto from "../../assets/kundelek.jpg";
import Kundelek2Photo from "../../assets/kundelek2.jpg";
import AdoptDogView from "../AdoptDogView/AdoptDogView";
import MainView from "../MainView/MainView";

const initialState = [
	{
		dogName: "Czarek",
		age: "5 years",
		placeOfStay: "Kraków",
		breed: "Spaniel",
		img: SpanielPhoto,
		index: 0,
	},
	{
		dogName: "Persil",
		age: "4 years",
		placeOfStay: "Nowa Huta",
		breed: "Shiba",
		img: ShibaPhoto,
		index: 1,
	},
	{
		dogName: "Mimi",
		age: "3 years",
		placeOfStay: "Nowa Huta",
		breed: "Owczarek Niemiecki",
		img: OwczarekPhoto,
		index: 2,
	},
	{
		dogName: "Kala",
		age: "9 years",
		placeOfStay: "Katowice",
		breed: "Husky",
		img: HuskyPhoto,
		index: 3,
	},
	{
		dogName: "Milka",
		age: "5 years",
		placeOfStay: "Wrocław",
		breed: "shiz-tzu",
		img: ShiztzuPhoto,
		index: 4,
	},
	{
		dogName: "Blanka",
		age: "10 years",
		placeOfStay: "Gdansk",
		breed: "Sznaucer-miniaturka",
		img: SznaucerPhoto,
		index: 5,
	},
	{
		dogName: "Felek",
		age: "5 years",
		placeOfStay: "Stalowa Wola",
		breed: "Kundelek",
		img: KundelekPhoto,
		index: 6,
	},
	{
		dogName: "Tofik",
		age: "7 years",
		placeOfStay: "Kalisz",
		breed: "Kundelek",
		img: Kundelek2Photo,
		index: 7,
	},
];
let index = 0;

class App extends Component {
	state = {
		dogs: initialState,
		dogsIndex: 0,
		dogsToAdopt: [],
	};

	handleClickHeart = () => {
		this.setState({
			dogsIndex: this.state.dogsIndex + 1,
			dogsToAdopt: [...this.state.dogsToAdopt, this.state.dogs[index]],
		});
		index++;
	};

	handleClickX = () => {
		this.setState({
			dogsIndex: this.state.dogsIndex + 1,
		});
		index++;
	};

	render() {
		const contextElements = {
			...this.state,
			handleClickHeart: this.handleClickHeart,
			handleClickX: this.handleClickX,
			index: index,
		};

		return (
			<BrowserRouter>
				<AppContext.Provider value={contextElements}>
					<ThemeProvider theme={theme}>
						<Header dogs={this.state.dogsToAdopt} />
						<Switch>
							<Route exact path="/" component={MainView} />
							<Route
								path="/adopt"
								component={AdoptDogView}
							/>
						</Switch>
					</ThemeProvider>
				</AppContext.Provider>
			</BrowserRouter>
		);
	}
}

export default App;
