import React from "react";
import styled from "styled-components";
import Paragraph from "../../Paragraph/Paragraph";
import Span from "../../Span/Span";
import DogContentContainer from "../../DogContentContainer/DogContentContainer";

const SmallImg = styled.img`
	width: 100%;
	height: 250px;
`;

const Items = styled.li`
	width: 400px;
	min-height: 300px;
	border: 2px solid ${({ theme }) => theme.main};
	margin: 0 10px 20px 10px;
`;

const ListItem = ({ dogName, age, placeOfStay, breed, img }) => {
	return (
		<Items>
			<SmallImg src={img} alt={dogName} />
			<DogContentContainer>
				<Paragraph secondary info="Dog Name: ">
					<Span secondary boldInfo={dogName}></Span>
				</Paragraph>
				<Paragraph secondary info="Age: ">
					<Span secondary boldInfo={age}></Span>
				</Paragraph>
				<Paragraph secondary info="Place of stay: ">
					<Span secondary boldInfo={placeOfStay}></Span>
				</Paragraph>
				<Paragraph secondary info="Breed: ">
					<Span secondary boldInfo={breed}></Span>
				</Paragraph>
			</DogContentContainer>
		</Items>
	);
};

export default ListItem;
