import React from "react";
import styled from "styled-components";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHeart, faTimes } from "@fortawesome/free-solid-svg-icons";
import Paragraph from "../Paragraph/Paragraph";
import Span from "../Span/Span";
import DogContentContainer from "../DogContentContainer/DogContentContainer";

const Wrapper = styled.div`
	height: 85vh;
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
`;

const HeartButton = styled.button`
	margin: 0 10px;
	background-color: transparent;
	border: none;
	cursor: pointer;
`;

const XButton = styled(HeartButton)``;

const PhotoDog = styled.img`
	border: 4px solid ${({ theme }) => theme.main};
	height: auto;
	width: 100%;
	height: 100%;
`;

const DogContainer = styled.div`
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
`;

const ImgContainer = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
	width: 450px;
	height: 450px;
`;

const Information = styled.p`
	font-size: 25px;
	color: red;

	@media (max-width: 600px) {
		font-size: 15px;
	}
`;

const Main = ({ dogs, dogsIndex, handlerX, handlerHeart }) => {
	return (
		<Wrapper>
			<DogContainer>
				<XButton disabled={dogs.length === dogsIndex}>
					<FontAwesomeIcon icon={faTimes} onClick={handlerX} />
				</XButton>
				<ImgContainer>
					{dogs.length === dogsIndex ? (
						<Information>
							We dont have dogs in your area
						</Information>
					) : (
						<PhotoDog
							src={dogs[dogsIndex].img}
							alt={dogs[dogsIndex].dogName}
						/>
					)}
				</ImgContainer>
				<HeartButton disabled={dogs.length === dogsIndex}>
					<FontAwesomeIcon
						icon={faHeart}
						onClick={handlerHeart}
					/>
				</HeartButton>
			</DogContainer>
			<DogContentContainer>
				{dogs.length === dogsIndex ? null : (
					<Paragraph info="Dog Name: ">
						<Span boldInfo={dogs[dogsIndex].dogName}></Span>
					</Paragraph>
				)}
				{dogs.length === dogsIndex ? null : (
					<Paragraph info="Age: ">
						<Span boldInfo={dogs[dogsIndex].age}></Span>
					</Paragraph>
				)}
				{dogs.length === dogsIndex ? null : (
					<Paragraph info="Place of stay: ">
						<Span
							boldInfo={dogs[dogsIndex].placeOfStay}
						></Span>
					</Paragraph>
				)}
				{dogs.length === dogsIndex ? null : (
					<Paragraph info="About: ">
						<Span boldInfo={dogs[dogsIndex].about}></Span>
					</Paragraph>
				)}
				{dogs.length === dogsIndex ? null : (
					<Paragraph info="Breed: ">
						<Span boldInfo={dogs[dogsIndex].breed}></Span>
					</Paragraph>
				)}
			</DogContentContainer>
		</Wrapper>
	);
};

export default Main;
