import React from "react";
import styled from "styled-components";
import { NavLink } from "react-router-dom";
import Logo from "../../assets/logo.jpg";

const Header = styled.nav`
	display: flex;
	justify-content: space-between;
	align-items: center;
	max-height: 15vh;
	width: 100%;
	padding: 10px 15px;
`;

const Img = styled.img`
	width: 100px;
	height: 100px;
	border: 4px solid ${({ theme }) => theme.main};
	border-radius: 50%;

	@media (max-width: 750px) {
		width: 70px;
		height: 70px;
	}

	@media (max-width: 680px) {
		width: 80px;
		height: 80px;
	}
`;
const Container = styled.nav`
	display: flex;
	justify-content: center;
	align-items: center;
`;

const NavigationContainer = styled.ul`
	list-style: none;
	display: flex;
	justify-content: center;
	align-items: center;
`;

const Link = styled(NavLink)`
	text-decoration: none;
	font-size: 20px;
	margin-right: 10px;
	color: ${({ theme }) => theme.secondary};
	transition: 0.5s;

	&:hover {
		color: ${({ theme }) => theme.main};
	}
`;

const LinkWithImg = styled(NavLink)`
	text-decoration: none;
	font-size: 20px;
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
`;

const Title = styled.h1`
	font-size: 50px;
	color: ${({ theme }) => theme.main};

	@media (max-width: 950px) {
		font-size: 30px;
	}

	@media (max-width: 680px) {
		font-size: 20px;
	}
	@media (max-width: 560px) {
		font-size: 18px;
	}

	@media (max-width: 500px) {
		font-size: 12px;
	}
`;

const DogLogo = styled.img`
	width: 150px;

	@media (max-width: 560px) {
		width: 110px;
	}

	@media (max-width: 500px) {
		width: 100px;
	}
`;

const NumbersOfDogs = styled.p`
	position: absolute;
	color: ${({ theme }) => theme.main};
	bottom: -20px;
	right: 50%;
	transform: translateX(50%);
`;

const Navigation = ({ dogs }) => {
	const randomNumber = Math.floor(Math.random() * dogs.length);
	console.log(dogs[randomNumber]);
	return (
		<Header>
			<DogLogo src={Logo}></DogLogo>
			<Title>Adopt a beautiful Dogs</Title>
			<Container>
				<NavigationContainer>
					<li>
						<Link exact to="/">
							Home
						</Link>
					</li>
					<li>
						{dogs.length ? (
							<LinkWithImg to="/adopt">
								<NumbersOfDogs>
									{dogs.length}
								</NumbersOfDogs>
								<Img src={dogs[randomNumber].img}></Img>
							</LinkWithImg>
						) : (
							<Link to="/adopt">Dogs</Link>
						)}
					</li>
				</NavigationContainer>
			</Container>
		</Header>
	);
};

export default Navigation;
